
<template lang="jade">

.body(id="boxWrap")
  m-box(v-for="box in boxes", :key="box.index", :type="box.type", :href="box.href", @click.native="normalToSpecia(box.index)").box

</template>
<script>

import _ from 'lodash/fp' 
import { mapGetters } from 'vuex'


//TODO: 限制盒子有效区域
export default {
  data () {
    return {
      size: 120,
      all: 0,
      col: -1,
      row: -1,
      allCol: -1,
      allRow: -1,
      boxes: [],
      specias: [
        { index: 5, href: 'land'}
      ]
    }
  },
  mounted () {
    this.initWrap()
    
    //窗口变化时重新渲染
    window.addEventListener('resize', _.debounce(200)(this.initWrap))
  },
  methods: {
    initWrap () {
      let size = this.size
      const width = document.getElementById('app').clientWidth
      const height = document.getElementById('app').clientHeight
      
      const col = Math.floor(width / size) 
      const row = Math.floor(height / size)
      const allCol = Math.ceil(width / size)
      const allRow = Math.ceil(height / size)
      
      document.getElementById('boxWrap').style.width = allCol * size + 'px'
      
      this.col = col 
      this.row = row
      this.allCol = allCol
      this.allRow = allRow
      this.all = allCol * allRow

      this.checkSpeciaIndex()
      this.createNormal()
      this.createDisabled()
      this.createSpecia()
    },

    createNormal () {
      const length = this.all
      let boxes = []
      for(let num = 0; num < length; num++ ) {
        boxes.push({index: num, type: 'normal'})
      }
      
      return this.boxes = boxes
    },

    /**
        创建特殊盒子
     */
    createSpecia () {
      let boxes = this.boxes
      let specias = this.specias
      specias.forEach(specia => { 
        let index = specia.index 
        boxes[index].type = 'specia'
        boxes[index].href = specia.href
      })
      
      this.specias = specias 
      this.boxes = boxes
    },

    /**
      创建不可用的块
     */
     createDisabled () {
       let col = this.col 
       let row = this.row
       let allCol = this.allCol
       let allRow = this.allRow
       let boxes = this.boxes

       // 如果用列数、行数小于边界, 则创建不可用块
       if (col < allCol || row < allRow) {
         boxes.forEach(box => {
            if ((box.index % allCol == col) || (box.index > col * row + 1)) {
              boxes[box.index].type = 'disabled'
            }
         })
         this.boxes = boxes
       }

     },

    /**
        修正特殊块的索引

        @param specias: 待修正的特殊块信息
        @param col: 可用的列数
        @param allCol: 所有列数
        @return: 一个修正了索引的specias
     */
    checkSpeciaIndex () {
      let specias = this.specias
      let col = this.col
      let allCol = this.allCol

      if (col < allCol) {
        return specias.map(specia => {
          let index = specia.index
          specia.index = index + Math.floor(index / col)
          return specia
        })
      } else return specias
    }, 

    normalToSpecia (index) {
      let boxes = this.boxes
      if (boxes[index].type === 'disabled') {
        return;
      } else {
        boxes[index].type = 'specia'
        return this.boxes = boxes
      }
    }
  }

  //TODO create gooey
  //TODO add color box

}
  
</script>
<style lang="stylus" scoped>
@import '../styles/main.styl'


.body 
  font-size 0px
  height 100%
  display flex 
  flex-flow row wrap
.box 
  font-size 16px
</style>


